@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap");
:root {
  --wrapper-padding: 24px;
}
@media (min-width: 1025px) {
  :root {
    --wrapper-padding: 60px;
  }
}

body {
  font-family: "Inter", sans-serif;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: #242424;
}

.logo, h1, h2, h3, h4, h5, h6 {
  font-family: "Rubik", sans-serif;
  color: #242424;
}

h1 {
  font-size: 3rem;
}
@media (max-width: 769px) {
  h1 {
    font-size: 2rem;
  }
}

h2 {
  font-size: 1.875rem;
}

.title {
  text-align: center;
  line-height: 2.14;
  padding-top: 9rem;
  padding-bottom: 9rem;
}

p {
  color: #4d4d4d;
  line-height: 1.625;
}

.logo {
  text-decoration: none;
  font-weight: 700;
  font-size: 1.25rem;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #242424;
  border-radius: 0.25rem;
  font-weight: 700;
  color: #fff;
  background: #242424;
  padding: 1rem 2rem;
}
.btn.outline {
  background: none;
  color: #242424;
  border: 0.0625rem solid #a3a3a3;
}

a.contact {
  border-radius: 100vmax;
  background: #242424;
  color: #fff;
  font-weight: 500;
  transition: 0.15s;
  padding: 0.75rem 1.25rem;
}
@media (min-width: 426px) {
  a.contact {
    padding: 0.6rem 1rem;
  }
}
a.contact:hover {
  background: #3e3e3e;
}

.wrapper {
  width: 100%;
  max-width: 1440px;
  padding-left: var(--wrapper-padding);
  padding-right: var(--wrapper-padding);
  margin-left: auto;
  margin-right: auto;
}

@media (min-width: 769px) {
  main.wrapper {
    padding-top: 0 !important;
  }
}

.loader {
  position: fixed;
  z-index: 1000;
  width: 100%;
  height: 100%;
  background: #fff;
}
.loader::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50px;
  height: 50px;
  border: 3px solid #e6e6e6;
  border-top-color: #242424;
  border-radius: 50%;
  -webkit-animation: 1s loader linear infinite;
          animation: 1s loader linear infinite;
}
@-webkit-keyframes loader {
  from {
    transform: translate(-50%, -50%) rotate(0);
  }
  to {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
@keyframes loader {
  from {
    transform: translate(-50%, -50%) rotate(0);
  }
  to {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

header {
  position: relative;
  z-index: 999;
  background: #fff;
  position: fixed;
  top: 0;
}
@media (min-width: 769px) {
  header {
    left: 50%;
    transform: translateX(-50%);
  }
}
header > .wrapper {
  display: flex;
  border-bottom: 0.0625rem solid transparent;
  transition: 0.15s;
}
@media (max-width: 769px) {
  header > .wrapper {
    padding: 0.5rem 0;
  }
}
@media (min-width: 769px) {
  header > .wrapper {
    padding: 0.8rem 0;
  }
}
header input {
  display: none;
}
header .logo {
  display: flex;
  align-items: center;
  padding-left: 0.75rem;
  padding-right: 0.75rem;
  margin-left: -0.75rem;
  margin-right: auto;
}
header ul {
  display: flex;
}
@media (max-width: 769px) {
  header ul {
    visibility: hidden;
    opacity: 0;
    background: #fff;
    border: 0.0625rem solid rgba(0, 0, 0, 0.1);
    border-radius: 0.32rem;
    position: fixed;
    top: 0.5rem;
    right: 0.72rem;
    background: #fff;
    flex-flow: column;
    justify-content: center;
    transition: 0.15s;
    padding: 1.9rem 0;
  }
}
@media (min-width: 769px) {
  header ul {
    align-items: center;
    gap: 0.2rem;
  }
}
header ul li {
  width: 100%;
  display: flex;
  justify-content: center;
  white-space: nowrap;
}
@media (max-width: 769px) {
  header ul li a:not(.contact) {
    padding: 0.75rem 3rem;
  }
}
@media (min-width: 769px) {
  header ul li a {
    padding: 0.6rem 0.8rem;
  }
}
@media (max-width: 769px) {
  header ul li a.contact {
    margin-top: 0.4rem;
  }
}
@media (min-width: 769px) {
  header ul li a.contact {
    margin-left: 0.5rem;
  }
}
header input:checked ~ ul {
  visibility: visible;
  opacity: 1;
  padding: 2rem 0;
}
header label {
  cursor: pointer;
  position: relative;
  width: 48px;
  height: 48px;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  padding: 12px;
  margin-right: -12px;
}
@media (min-width: 769px) {
  header label {
    display: none;
  }
}
header label::before, header label::after {
  content: "";
  width: 18px;
  height: 2.4px;
  background: #242424;
  transition: 0.15s;
}
header label::before {
  margin-bottom: 6px;
}
header input:checked ~ label::before {
  margin-bottom: -2px;
  transform: rotate(45deg);
}
header input:checked ~ label::after {
  transform: rotate(-45deg);
}

footer > .wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 0.0625rem solid rgba(0, 0, 0, 0.1);
  padding: 2rem 0;
  margin-top: 3rem;
}
footer .languages {
  position: relative;
}
footer .languages input[type=checkbox] {
  position: absolute;
  opacity: 0;
  pointer-events: none;
}
footer .languages input[type=checkbox]:checked ~ .list {
  opacity: 1;
  visibility: visible;
}
footer .languages input[type=checkbox]:checked ~ label {
  background: #F7F7F7;
}
footer .languages label {
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.25rem;
  border-radius: 0.2rem;
  padding: 0.6rem 0.8rem;
  margin-left: -0.8rem;
}
footer .languages label:hover {
  background: #F7F7F7;
}
footer .languages label ion-icon {
  --ionicon-stroke-width: 2.7rem;
}
footer .languages label span {
  font-weight: 600;
}
footer .languages label img {
  width: 0.5rem;
}
footer .languages .list {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  bottom: 100%;
  left: 10%;
  background: #fff;
  border: 0.0625rem solid rgba(0, 0, 0, 0.1);
  border-radius: 0.32rem;
  transition: 0.15s;
  padding: 0.25rem;
}
footer .languages .list .lang {
  cursor: pointer;
  min-width: 12rem;
  border-radius: 0.07rem;
  padding: 0.32rem 0.5rem;
}
footer .languages .list .lang:hover {
  background: #F7F7F7;
}
footer .languages .list .lang div {
  pointer-events: none;
  white-space: nowrap;
  line-height: 1.5;
}
footer .languages .list .lang div:nth-child(1) {
  font-weight: 600;
}
footer .languages .list .lang div:nth-child(2) {
  color: #4d4d4d;
}
footer .links {
  display: flex;
  gap: 1rem;
}
footer .links a {
  display: flex;
  align-items: center;
}
footer .links img {
  width: 1rem;
}

@media (max-width: 769px) {
  section.hero {
    padding-top: 1.5rem;
  }
}
@media (min-width: 769px) {
  section.hero {
    height: 100vh;
    display: flex;
    align-items: center;
    gap: 0 2rem;
  }
}
section.hero .pfp {
  width: 100%;
  aspect-ratio: 1/1;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  filter: grayscale(100%) brightness(135%);
  position: relative;
  z-index: -1;
}
@media (max-width: 769px) {
  section.hero .pfp {
    max-width: 20rem;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2rem;
  }
}
section.hero p {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}
section.hero a.contact {
  display: inline-block;
  padding: 0.75rem 1.25rem;
}
section.hero .arrow-down {
  position: absolute;
  bottom: 5%;
  transition: 0.15s;
  -webkit-animation: 1s arrow_down ease-in alternate infinite;
          animation: 1s arrow_down ease-in alternate infinite;
  left: 50%;
}
@media (max-width: 769px) {
  section.hero .arrow-down {
    display: none;
  }
}
@-webkit-keyframes arrow_down {
  from {
    transform: translate(-50%, -10%);
  }
  to {
    transform: translate(-50%, 10%);
  }
}
@keyframes arrow_down {
  from {
    transform: translate(-50%, -10%);
  }
  to {
    transform: translate(-50%, 10%);
  }
}
section.hero .arrow-down ion-icon {
  font-size: 2rem;
}
section.hero .arrow-down img {
  width: 2rem;
}

@media (max-width: 769px) {
  section.about {
    margin-top: 5rem;
  }
}
section.about .container {
  background: rgba(0, 0, 0, 0.02);
  border-radius: 3rem;
  padding: 3rem;
}
@media (min-width: 426px) {
  section.about .container {
    padding: 4rem;
  }
}
@media (min-width: 769px) {
  section.about .container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    gap: 0 2rem;
    padding: 5rem;
  }
}
@media (min-width: 769px) {
  section.about .title {
    grid-column: 3/1;
  }
}
section.about img {
  display: block;
  width: 6rem;
}
section.about h2 {
  margin-top: 1.2rem;
  margin-bottom: 0.75rem;
}
section.about ul {
  display: grid;
  gap: 1.25rem;
}
@media (max-width: 769px) {
  section.about ul {
    padding-top: 2.5rem;
  }
}
section.about ul li {
  position: relative;
}
section.about ul li span {
  position: absolute;
  bottom: 0.8125rem;
  transform: translateX(-50%);
  font-size: 0.7rem;
  color: #fff;
  background: #242424;
  border-radius: 0.2rem;
  padding: 0.5rem;
}
section.about ul li span::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 100%);
  border: 0.1875rem solid transparent;
  border-top-color: #242424;
}
section.about ul li div {
  position: relative;
  height: 10px;
  background: #f0f0f0;
  margin-top: 0.375rem;
}
section.about ul li div::before {
  content: "";
  position: absolute;
  background: #242424;
  height: 100%;
}
section.about ul li:nth-child(1) span {
  left: 97%;
}
section.about ul li:nth-child(1) span::before {
  content: "97%";
}
section.about ul li:nth-child(1) div::before {
  width: 97%;
}
section.about ul li:nth-child(2) span {
  left: 98%;
}
section.about ul li:nth-child(2) span::before {
  content: "98%";
}
section.about ul li:nth-child(2) div::before {
  width: 98%;
}
section.about ul li:nth-child(3) span {
  left: 56%;
}
section.about ul li:nth-child(3) span::before {
  content: "56%";
}
section.about ul li:nth-child(3) div::before {
  width: 56%;
}
section.about ul li:nth-child(4) span {
  left: 76%;
}
section.about ul li:nth-child(4) span::before {
  content: "76%";
}
section.about ul li:nth-child(4) div::before {
